module.exports = {
    /**
     * Get current user information
     */
    CURRENT_USER: 'CURRENT_USER',

    /**
     * Return list of all users
     */
    USERS: 'USERS',

    /**
     * Save user
     */
    USER_SAVE: 'USER_SAVE',

    /**
     * Change online status of user
     */
    ONLINE: 'ONLINE',

    /**
     * Create new room
     */
    CREATE_ROOM: 'CREATE_ROOM',

    /**
     * Return all rooms
     */
    ROOMS: 'ROOMS',

    /**
     * Current user rooms
     */
    CURRENT_USER_ROOMS: 'CURRENT_USER_ROOMS',

    /**
     * Join current user to the room
     */
    CURRENT_USER_JOIN_ROOM: 'CURRENT_USER_JOIN_ROOM',

    /**
     * Leave room
     */
    CURRENT_USER_LEAVE_ROOM: 'CURRENT_USER_LEAVE_ROOM',

    /**
     * Join user to the room
     */
    USER_JOIN_ROOM: 'USER_JOIN_ROOM',

    /**
     * User joined to room
     */
    USER_JOINED: 'USER_JOINED',

    /**
     * User leave the room
     */
    USER_LEAVED: 'USER_LEAVED',

    /**
     * Send message
     */
    SEND_MESSAGE: 'SEND_MESSAGE',

    /**
     * Show list of messages
     */
    MESSAGES: 'MESSAGES',

    /**
     * New message coming
     */
    MESSAGE: 'MESSAGE',

    /**
     * Server error
     */
    ERROR: 'ERROR'
};



